import * as React from "react";

import { Routes, Route } from 'react-router-dom';
import { ThemeProvider } from '@mui/material/styles';
// public routes
import SignUp from "pages/private/SignUp";
import SignIn from "pages/private/SignIn";
import Main from "pages/public/Main";
// components
import TopMenu from "components/TopMenu";
// private routes
import PrivateRoute from "pages/private/PrivateRoute";
import Join from "pages/peasant/Join";
import Members from 'pages/secretary/Members';
// helpers
import { Roles } from 'types/Enums';
import theme from "./theme";

import './App.css';

// router
// https://github.com/remix-run/react-router/blob/main/docs/start/tutorial.md
// https://blog.logrocket.com/complete-guide-authentication-with-react-router-v6/

export default function App() {
  return (
    <ThemeProvider theme={theme}>
      <TopMenu />
      <div className="App">
        <Routes>
          <Route path="/" element={<Main />} />
          <Route path="/register" element={<SignUp />} />
          <Route path="/login" element={<SignIn />} />
          <Route path="/join" 
            element={ <PrivateRoute> <Join userRole = {Roles.Peasant}/> </PrivateRoute> }
          />
          <Route path="/members" 
            element={ <PrivateRoute> <Members userRole = {Roles.Secretary}/> </PrivateRoute> }
          />
        </Routes>
      </div>
    </ThemeProvider>
  );
}
