import React, { useState } from 'react';

import dayjs, { Dayjs } from 'dayjs';

import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { MobileDatePicker } from '@mui/x-date-pickers/MobileDatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';

import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';

import { useAuth } from 'hooks/useAuth';

import Api from 'helpers/Api';

export default function Join(args: any) {
  const { user } = useAuth();

  const [isSend, setIsSend] = useState(false);

  const [birthDate, setBirthDate] = React.useState<Dayjs | null>(
    dayjs('2000-12-01T21:11:54'),
  );

  const changeBirthDate = (newValue: Dayjs | null) => {
    setBirthDate(newValue);
  };

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    const form = new FormData(event.currentTarget);

    const data = {
      nickname: form.get('nickname'),
      first_name: form.get('firstName'),
      last_name: form.get('lastName'),
      futher_name: form.get('futherName'),
      phone: form.get('phone'),
      email: user?.email,
      birth_date: birthDate,
      vk: form.get('vk'),
      telegram: form.get('telegram'),
      about: form.get('about'),
    }

    await Api.join(data, user);
    setIsSend(true);
  }

  return (
    <LocalizationProvider dateAdapter={AdapterDayjs}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        {!isSend && (<Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Typography component="h1" variant="h5">
                
          </Typography>
          <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="nickname"
                  label=""
                  name="nickname"
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  autoComplete="given-name"
                  name="firstName"
                  required
                  fullWidth
                  id="firstName"
                  label=""
                  autoFocus
                  defaultValue={user?.firstName}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  required
                  fullWidth
                  id="lastName"
                  label=""
                  name="lastName"
                  autoComplete="family-name"
                  defaultValue={user?.lastName}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="futherName"
                  label=""
                  name="futherName"
                  autoComplete="futher-name"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="phone"
                  label=""
                  name="phone"
                  autoComplete="phone"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="email"
                  label="Email"
                  name="email"
                  value={user?.email}
                  disabled={true}
                />
              </Grid>
              <Grid item xs={12}>
                <MobileDatePicker
                  label=" "
                  inputFormat="DD/MM/YYYY"
                  value={birthDate}
                  onChange={changeBirthDate}
                  renderInput={(params) => <TextField fullWidth {...params} />}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="telegram"
                  label="Telegram"
                  name="telegram"
                  autoComplete="telegram"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="vk"
                  label="   Vk"
                  name="vk"
                  autoComplete="vk"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  multiline
                  fullWidth
                  id="about"
                  label=" "
                  name="about"
                />
              </Grid>
            </Grid>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
               
            </Button>
          </Box>
        </Box>)}

        {isSend && (
          <Typography>
               
          </Typography>
        )}

      </Container>
    </LocalizationProvider>
  )
}
