import React from 'react'
import Typography from "@mui/material/Typography";
import AppBar from "@mui/material/AppBar";
import Button from "@mui/material/Button";
import Toolbar from "@mui/material/Toolbar";
import LinkUi from "@mui/material/Link";
import { useAuth } from 'hooks/useAuth';
import PagesConfig from 'types/Pages';

export default function TopMenu() {
    const { user, logout } = useAuth();
    console.log(user);
    return (
        <AppBar
            position="static"
            color="default"
            elevation={0}
            sx={{ borderBottom: (theme) => `1px solid ${theme.palette.divider}` }}
        >
            <Toolbar sx={{ flexWrap: "wrap" }}>
                <Typography variant="h6" color="inherit" noWrap sx={{ flexGrow: 1 }}>
                    <LinkUi href="/"> </LinkUi>
                </Typography>

                {!user ? (
                    <React.Fragment>
                        <Button variant="outlined" sx={{ my: 1, mx: 1.5 }}>
                            <LinkUi href="/login"></LinkUi>
                        </Button>

                        <Button variant="outlined" sx={{ my: 1, mx: 1.5 }}>
                            <LinkUi href="/register"></LinkUi>
                        </Button>
                    </React.Fragment>
                ) : null}

                {!!user && (
                    <>
                        {Object.values(PagesConfig).map(({ url, role, name }: any, i) => {
                            return role && user.role === role ?
                                <Button key={i} variant="outlined" sx={{ my: 1, mx: 1.5 }}>
                                    <LinkUi href={url}>{name}</LinkUi>
                                </Button> : null
                        })}
                        <Button key={'logout'} onClick={logout} variant="outlined" sx={{ my: 1, mx: 1.5 }}>
                            <Typography ></Typography>
                        </Button>
                    </>
                )}
            </Toolbar>
        </AppBar>
    )
}
